/* ======= GLOBAL ======= */
body p {
  font-weight: 300;
  line-height: 1.6923;
}
:target:before {
  content: "";
  display: block;
  height: 80px;
  /* fixed header height*/
  margin: -80px 0 0;
  /* negative fixed header height */
}
select:focus {
  outline-style: none;
}
input::-webkit-input-placeholder {
  color: #aaa;
}
input::-moz-placeholder {
  color: #aaa;
}
img.pull-right {
  padding-left: 20px;
}
img.pull-left {
  padding-right: 20px;
}
.main-content {
  margin-bottom: 110px;
}
.bg-primary {
  background-color: #00B0B8;
}
.bg-secondary {
  background-color: #181818;
}
.block-title {
  position: relative;
  display: block;
  margin-bottom: 40px;
  overflow: hidden;
  text-align: center;
}
.block-title:before {
  position: absolute;
  bottom: -3vw;
  width: 50.1%;
  height: 8vw;
  background-color: #f5f5f5;
  z-index: 101;
  content: '';
  left: 0;
  transform: skewY(4.5deg);
}
.block-title:after {
  position: absolute;
  bottom: -3vw;
  width: 50.1%;
  height: 8vw;
  background-color: #f5f5f5;
  z-index: 101;
  content: '';
  right: 0;
  transform: skewY(-4.5deg);
}
.block-title .block-title-content {
  position: relative;
  padding-top: 140px;
  padding-bottom: 80px;
}
.block-title .block-title-title {
  margin-bottom: 30px;
  padding-right: 10px;
  padding-left: 10px;
  color: #fff;
  font-family: Ubuntu;
  font-size: 34px;
  font-weight: 700;
  text-transform: uppercase;
}
.block-title .breadcrumb {
  text-transform: uppercase;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 0;
}
.br {
  display: block;
}
.rtd {
  position: relative;
}
/* ======= BASIC ELEMENTS ======= */
.decor.decor-1 {
  width: 30px;
  margin-bottom: 20px;
  border-top: 1px solid #ccc;
}
.decor.decor-1:after {
  display: block;
  width: 30px;
  margin-top: 2px;
  margin-left: 10px;
  border-top: 1px solid #ccc;
  content: '';
}
.decor.decor-1_mod-a {
  border-top: 1px solid rgba(204, 204, 204, 0.8);
}
.decor.decor-1_mod-a:after {
  border-top: 1px solid rgba(204, 204, 204, 0.8);
}
.decor.decor-1_mod-b {
  display: inline-block;
  width: 170px;
  margin: 17px 0 20px -15px;
}
.decor.decor-1_mod-b:after {
  width: 170px;
  margin-left: 20px;
}
.decor.decor-1_mod-c {
  margin-top: 17px;
  margin-bottom: 30px;
  margin-left: 30px;
}
.decor.decor-2 {
  position: relative;
  display: inline-block;
  margin-top: 6px;
  text-align: center;
}
.decor.decor-2:before {
  position: absolute;
  top: 5px;
  display: block;
  width: 14px;
  height: 14px;
  content: '';
  transform: rotate(45deg);
  right: -10px;
}
.decor.decor-2:after {
  position: absolute;
  top: 5px;
  display: block;
  width: 14px;
  height: 14px;
  content: '';
  transform: rotate(45deg);
  left: -10px;
}
.decor.decor-2 .icon {
  position: relative;
  display: inline-block;
  width: 23px;
  height: 23px;
  font-size: 12px;
  line-height: 1;
  color: #555555;
  border: 3px solid #f5f5f5;
  border-radius: 3px;
  background-color: #ddd;
  z-index: 1;
  transform: rotate(45deg);
}
.decor.decor-2 .icon:before {
  display: block;
  margin: 3px 0 0 1px;
  transform: rotate(-45deg);
}
.decor.decor-3 {
  display: inline-block;
  width: 14px;
  height: 14px;
  margin-right: 14px;
  margin-left: 3px;
  font-size: 8px;
  vertical-align: middle;
  border: solid 1px #ccc;
  background-color: #fff;
  transform: rotate(45deg);
  text-align: left;
  color: #00B0B8;
}
.decor.decor-3:before {
  display: block;
  margin: 4% 0 0 32%;
  line-height: 1;
  transform: rotate(-45deg);
}
.accordion .btn-collapse .glyphicon:before {
  content: "\e260";
}
.accordion .btn-collapse.collapsed .glyphicon:before {
  content: "\e259";
}
