}
.section-bg.section-bg-left:before {
  background: no-repeat 0 100%;
}
.section-bg.section-bg-right:before {
  background: no-repeat 100% 100%;
}
.section-bg.section-bg-opaque:before {
  opacity: 1;
}
.section-bg.section-bg-1:before {
  background-image: url('../img/bg/1.png');
}
.section-bg.section-bg-2:before {
