.section-top:after {
  position: absolute;
  top: -16px;
  width: 170px;
  height: 80px;
  content: '';
  right: -70px;
  -webkit-box-shadow: -4px 1px 0 2px #f5f5f5;
  box-shadow: -4px 1px 0 2px #f5f5f5;
  transform: skew(58deg, -10deg);
